.toggle_btns {
  user-select: none;
    display: flex;
    align-items: center;
    justify-content: start;
    list-style: none;
    border-bottom: 3px solid #ccc;
    padding-inline: 32px;
    gap: 76px;
    overflow-x: auto;
    scrollbar-width: none;
    -ms-overflow-style: none;
    margin: auto;
    &:active{
      cursor: grabbing;
    }
    &::-webkit-scrollbar {
      display: none;
    }

    li {
      padding-block: 32px 0;
      position: relative;

      a {
        position: relative;
        display: inline-block;
        font-size: var(  --title-font-normal);
        color: #383838;
        font-weight: 600;
        white-space: nowrap;
        border-bottom: 5px solid #cccccc00;
        padding-block: 12px 16px;
        padding-inline: 12px;
        transition: 0.2s ease-in;
      }

      .active {
        border-bottom: 5px solid var(--main-color);
      }
    }
  }