.toggle_btns {
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  display: flex;
  align-items: center;
  justify-content: start;
  list-style: none;
  border-bottom: 3px solid #ccc;
  padding-inline: 32px;
  gap: 76px;
  overflow-x: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
  margin: auto;
}
.toggle_btns:active {
  cursor: grabbing;
}
.toggle_btns::-webkit-scrollbar {
  display: none;
}
.toggle_btns li {
  padding-block: 32px 0;
  position: relative;
}
.toggle_btns li a {
  position: relative;
  display: inline-block;
  font-size: var(--title-font-normal);
  color: #383838;
  font-weight: 600;
  white-space: nowrap;
  border-bottom: 5px solid rgba(204, 204, 204, 0);
  padding-block: 12px 16px;
  padding-inline: 12px;
  transition: 0.2s ease-in;
}
.toggle_btns li .active {
  border-bottom: 5px solid var(--main-color);
}/*# sourceMappingURL=ToggleButtons.css.map */