.features-section {
  padding-inline: 4%;
  margin-top: 48px;
}
.features-section .features-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-block: 48px;
  gap: 48px;
}
.features-section .features-list .feature-card {
  max-width: 270px;
  display: flex;
  flex-direction: column;
  gap: 24px;
  padding: 24px 16px;
  border-radius: 12px;
  box-shadow: 0px 0px 32px 0px #e5e5e5;
  margin-inline: auto;
}
.features-section .features-list .feature-card .feature-image {
  display: flex;
  justify-content: center;
}
.features-section .features-list .feature-card h2 {
  font-weight: 500;
  font-size: var(--title-font-three);
  text-align: center;
  color: var(--main-color);
}
.features-section .features-list .feature-card .feature-description {
  font-size: var(--title-font-descrption-two);
  text-align: center;
}

.more_feuters {
  background-color: var(--background-color);
  padding-bottom: 124px;
  padding-inline: 4%;
  gap: 0;
}
.more_feuters .content-wrapper {
  max-width: 100%;
}
.more_feuters .content-section {
  padding-top: 46px;
}
.more_feuters .image-container {
  width: 50%;
  display: flex;
  justify-content: center;
}
.more_feuters .image-container .photo {
  width: 80%;
  max-width: 380px;
  position: relative;
  font-size: var(--title-font-descrption-three);
  font-weight: 500;
}
.more_feuters .image-container .photo .before {
  display: flex;
  align-items: start;
  gap: 12px;
  width: 224px;
  position: absolute;
  background-color: var(--background-color);
  color: var(--text-color);
  padding: 12px;
  inset-inline-end: -10%;
  top: 20%;
  border-radius: 12px 12px 0px 12px;
  box-shadow: 0px 0px 24px 0px #909090;
}
.more_feuters .image-container .photo .after {
  display: flex;
  align-items: start;
  gap: 12px;
  width: 224px;
  position: absolute;
  background-color: var(--background-color);
  color: var(--text-color);
  padding: 12px;
  inset-inline-start: -10%;
  bottom: 20%;
  border-radius: 0px 12px 12px 12px;
  box-shadow: 0px 0px 12px 0px #909090;
}

.learn {
  position: relative;
  padding-top: 48px;
  padding-inline: 4%;
  background-color: #fafded;
}
.learn .section-container {
  padding-top: 32px;
}
.learn .section-container .content-wrapper {
  width: 50%;
}
.learn .section-container .content-wrapper .section-title {
  font-size: var(--title-font-two);
  padding-bottom: 24px;
  font-weight: 500;
  color: #383838;
  line-height: 1.8;
}
.learn .section-container .content-wrapper .list .icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  background-color: var(--main-color);
  padding: 6px;
  border-radius: 50%;
  color: white;
}
.learn .section-container .content-wrapper .list .list-item {
  font-size: var(--title-font-normal);
  opacity: 0.5;
  transition: 0.4s;
  cursor: pointer;
}
.learn .section-container .content-wrapper .list .active {
  opacity: 1;
}
.learn .image-container {
  display: flex;
  justify-content: center;
  width: 50%;
  padding-top: 32px;
}
.learn .image-container .photo {
  display: flex;
  align-items: center;
  width: 230px;
  height: 475px;
  border-radius: 30px;
  box-shadow: 0px 0px 24px 0px #ccc;
  left: 0;
  overflow: hidden;
  position: relative;
}
.learn .image-container .photo .photo_content {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  position: absolute;
  transition: 0.6s;
}
.learn .image-container .photo .photo_content video {
  top: 0;
  right: 50%;
  width: 100%;
  height: 100%;
  position: relative;
}
.learn .btn {
  display: none;
  cursor: pointer;
  position: absolute;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: var(--main-color);
  color: var(--background-color);
  transform: translateY(-50%);
  z-index: 2;
}
.learn .next {
  inset-inline-start: 0;
  top: 300px;
}
.learn .back {
  inset-inline-end: 0;
  top: 300px;
}

.map {
  padding-top: 48px;
  padding-inline: 4%;
}
.map .map_content {
  width: 100%;
  max-width: 960px;
  margin-inline: auto;
  position: relative;
}
.map .map_content .map_photo {
  width: 100%;
}
.map .map_content .target {
  position: absolute;
  transform: translate(-50%, -50%);
}
.map .map_content .target:hover .photo {
  display: block;
}
.map .map_content .target .photo {
  display: none;
  width: 200px;
  position: absolute;
  background-color: white;
  border: 1px solid #ccc;
  transform: translate(-50%, -50%);
  top: 330%;
  left: 420%;
  border-radius: 12px;
  transition: 0.4s;
  padding: 12px;
  animation: Fad_In 0.3s ease-in-out;
}
.map .map_content .target .photo p {
  font-size: 12px;
}
.map .map_content .target .photo ::after {
  content: "";
  width: 30%;
  position: absolute;
  height: 2px;
  background-color: var(--main-color);
  transform: translate(-50%, -50%) rotate(40deg);
  top: -30px;
  left: -13%;
}
.map .map_content .target .photo ::before {
  content: "";
  position: absolute;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  background-color: var(--main-color);
  transform: translate(-50%, -50%);
  top: -8px;
  left: 0px;
}
.map .map_content .target .photo img {
  width: 100%;
  border-radius: 12px;
  -o-object-fit: contain;
     object-fit: contain;
}
.map .map_content .test {
  width: 48px !important;
  height: 48px !important;
  border-radius: 12px;
  background-color: var(--background-color);
  padding: 8px;
}

.video-section {
  padding-top: 48px;
  padding-inline: 4%;
}
.video-section .video_content {
  width: 100%;
  margin-block: 64px;
  position: relative;
}
.video-section .video_content video {
  width: 100%;
  height: 100%;
  border: 1px solid var(--main-color);
  border-radius: 12px;
}
.video-section .video_content .icon {
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (max-width: 900px) {
  .more_feuters .image-container {
    width: 100%;
    justify-content: center;
  }
  .learn .btn {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .learn .section-container {
    width: 100%;
  }
  .learn .section-container .content-wrapper {
    width: 100%;
  }
  .learn .section-container .content-wrapper .section-title {
    display: none;
  }
  .learn .section-container .content-wrapper .content-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 100%;
  }
  .learn .section-container .content-wrapper .list {
    justify-content: center;
    text-align: center;
  }
  .learn .section-container .content-wrapper .list .list-item {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .learn .section-container .content-wrapper .list .list-item:not([id=active]) {
    display: none;
  }
  .learn .image-container {
    width: 100%;
  }
}
@media screen and (max-width: 1024px) {
  .map .map_content .target {
    position: absolute;
    transform: translate(-50%, -50%);
  }
  .map .map_content .target:hover .photo {
    visibility: visible;
    opacity: 1;
  }
  .map .map_content .target .photo {
    position: static;
    width: 200px;
    position: absolute;
    top: 50%;
    left: 50%;
  }
  .map .map_content .target .photo ::after {
    display: none;
  }
  .map .map_content .target .photo ::before {
    display: none;
  }
  .map .map_content .target .photo img {
    width: 100%;
    border-radius: 12px;
    -o-object-fit: contain;
       object-fit: contain;
  }
}
@keyframes Fad_In {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}/*# sourceMappingURL=Home.css.map */