.section-two-container {
  display: flex;
  align-items: center;
  justify-content: start;
  padding-inline: 4%;
  margin-block: 124px;
  gap: 48px;
}
.section-two-container .content-wrapper {
  width: 60%;
  max-width: 900px;
}
.section-two-container .content-wrapper .section-title {
  font-size: var(--title-font-normal);
  font-weight: 600;
  margin-bottom: 32px;
}
.section-two-container .content-wrapper .section-title span {
  color: var(--main-color);
}
.section-two-container .content-wrapper .section-description {
  font-size: var(--title-font-descrption);
}
.section-two-container .content-wrapper .section-description span {
  color: var(--main-color);
}
.section-two-container .image-wrapper {
  display: flex;
  justify-content: center;
  width: 35%;
}
.section-two-container .image-wrapper img {
  width: 100%;
  max-width: 500px;
}

.section-left {
  justify-content: space-between;
  background-color: #fafded;
}

.question {
  padding-inline-start: 0 !important;
}
.question .image-wrapper img {
  max-width: none;
}

@media screen and (max-width: 900px) {
  .section-two-container {
    flex-direction: column;
  }
  .section-two-container .content-wrapper {
    padding: 4%;
    width: 100%;
  }
  .section-two-container .image-wrapper {
    width: 100%;
  }
  .section-left {
    flex-direction: column-reverse;
  }
}/*# sourceMappingURL=SectionTwo.css.map */