.download_links {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 48px;
    margin-block:48px ;
}
.download_links a button {
    cursor: pointer;
    width: 240px;
    padding: 8px;
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: start;
    gap: 12px;
    border: none;
    background-color: #3D3D3D;
    color: white;
    font-size: 14px;
    box-shadow: 0px 0px 4px 1px #ccc;
}
.download_links a button span{
    line-height: 1.4;
}
.download_links a button span:nth-child(2){
    font-size: 20px;

}
.download_links button .text {
    display: flex;
    flex-direction: column;
}
.download_links a:nth-of-type(2) button{
    color: black;
    background-color: white;

}
