.section-container {
  display: flex;
  align-items: start;
  justify-content: space-between;
  padding-block: 69px;
  padding-inline-start: 4%;
  gap: 16px;
  background-color: #FAFDED;
}
.section-container .content-wrapper {
  width: 60%;
}
.section-container .content-wrapper .content-section {
  margin-top: 37px;
  max-width: 630px;
}
.section-container .content-wrapper .content-section .section-title {
  font-size: var(--title-font);
  margin-bottom: 24px;
}
.section-container .content-wrapper .content-section .section-title span {
  color: var(--main-color);
}
.section-container .content-wrapper .content-section .list {
  list-style: none;
}
.section-container .content-wrapper .content-section .list .list-item {
  display: flex;
  align-items: start;
  font-size: var(--title-font-normal);
  font-weight: 500;
  gap: 12px;
  margin-top: 16px;
}
.section-container .image-container {
  width: 40%;
  display: flex;
  justify-content: end;
  max-width: 630px;
}
.section-container .image-container img {
  width: 100%;
  -o-object-fit: contain;
     object-fit: contain;
}
.section-container .image-container video {
  top: 4%;
  right: 25%;
  height: 92%;
  transform: translateX(49%);
  position: absolute;
  border-radius: 8%;
}

@media screen and (max-width: 900px) {
  .section-container {
    flex-direction: column-reverse;
  }
  .section-container .content-wrapper {
    width: 100%;
  }
  .section-container .image-container {
    width: 80%;
    margin-inline-start: auto;
  }
}/*# sourceMappingURL=MainSection.css.map */