.section-container {
    display: flex;
    align-items: start;
    justify-content: space-between;
    padding-block: 69px;
    padding-inline-start: 4%;
    gap: 16px;
    background-color: #FAFDED;

    .content-wrapper {
        width: 60%;

        .content-section {
            margin-top: 37px;
            max-width: 630px;

            .section-title {
                font-size: var(--title-font);
                margin-bottom: 24px;

                span {
                    color: var(--main-color);
                }
            }

            .list {
                list-style: none;

                .list-item {
                    display: flex;
                    align-items: start;
                    font-size: var(--title-font-normal);
                    font-weight: 500;
                    gap: 12px;
                    margin-top: 16px;
                }
            }
        }
    }

    .image-container {
        width: 40%;
        display: flex;
        justify-content: end;
        max-width: 630px;

        img {
            width: 100%;
            object-fit: contain;
        }
        video{
            top: 4%;
            right: calc(25%);
            height: calc(100% - 8%);
            transform: translateX(49%);
            position: absolute;
            border-radius: 8%;
        }
    }
}

@media screen and (max-width: 900px) {
    .section-container {
        flex-direction: column-reverse;

        .content-wrapper {
            width: 100%;
        }

        .image-container {
            width: 80%;
            margin-inline-start: auto;
        }
    }
}
