.section-two-container {
    display: flex;
    align-items: center;
    justify-content: start;
    padding-inline: 4%;
    margin-block: 124px;
    gap: 48px;
  
    .content-wrapper {
      width: 60%;
      max-width: 900px;
  
      .section-title {
        font-size: var(--title-font-normal);
        font-weight: 600;
        margin-bottom: 32px;
  
        span {
          color: var(--main-color);
        }
      }
  
      .section-description {
        font-size: var( --title-font-descrption);
  
        span {
          color: var(--main-color);
        }
      }
    }
  
    .image-wrapper {
        display: flex;
        justify-content: center;
        width: 35%;
  
      img {
        width: 100%;
        max-width: 500px;
      }
    }
  }
  .section-left{
    justify-content: space-between;
    background-color: #fafded;
  }
  .question{
    padding-inline-start:0 !important;
    .image-wrapper{
        img{
            max-width: none;
        }
    }
  }
  @media screen and (max-width: 900px) {
    .section-two-container  {
        flex-direction: column;
        .content-wrapper {
          padding: 4%;
          width: 100%;
        }
        .image-wrapper {
          width: 100%;
        }
      }
      .section-left{
        flex-direction: column-reverse;

      }
  }