.features-section {
  padding-inline: 4%;
  margin-top: 48px;
  .features-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-block: 48px;
    gap: 48px;
    .feature-card {
      max-width: 270px;
      display: flex;
      flex-direction: column;
      gap: 24px;
      padding: 24px 16px;
      border-radius: 12px;
      box-shadow: 0px 0px 32px 0px #e5e5e5;
      margin-inline: auto;
      .feature-image {
        display: flex;
        justify-content: center;
      }
      h2 {
        font-weight: 500;
        font-size: var(--title-font-three);
        text-align: center;
        color: var(--main-color);
      }

      .feature-description {
        font-size: var(--title-font-descrption-two);
        text-align: center;
      }
    }
  }
}

.more_feuters {
  background-color: var(--background-color);
  padding-bottom: 124px;
  padding-inline: 4%;
  gap: 0;

  .content-wrapper {
    max-width: 100%;
  }

  .content-section {
    padding-top: 46px;
  }

  .image-container {
    width: 50%;
    display: flex;
    justify-content: center;

    .photo {
      width: 80%;
      max-width: 380px;
      position: relative;
      font-size: var(--title-font-descrption-three);
      font-weight: 500;
      .before {
        display: flex;
        align-items: start;
        gap: 12px;
        width: 224px;
        position: absolute;
        background-color: var(--background-color);
        color: var(--text-color);
        padding: 12px;
        inset-inline-end: -10%;
        top: 20%;
        border-radius: 12px 12px 0px 12px;
        box-shadow: 0px 0px 24px 0px #909090;
      }

      .after {
        display: flex;
        align-items: start;
        gap: 12px;
        width: 224px;
        position: absolute;
        background-color: var(--background-color);
        color: var(--text-color);
        padding: 12px;
        inset-inline-start: -10%;
        bottom: 20%;
        border-radius: 0px 12px 12px 12px;
        box-shadow: 0px 0px 12px 0px #909090;
      }
    }
  }
}

.learn {
  position: relative;
  padding-top: 48px;
  padding-inline: 4%;
  background-color: #fafded;

  .section-container {
    padding-top: 32px;
    .content-wrapper {
      width: 50%;
      .section-title {
        font-size: var(--title-font-two);
        padding-bottom: 24px;
        font-weight: 500;
        color: #383838;
        line-height: 1.8;
      }

      .list {
        .icon-wrapper {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 36px;
          height: 36px;
          background-color: var(--main-color);
          padding: 6px;
          border-radius: 50%;
          color: white;
        }

        .list-item {
          font-size: var(--title-font-normal);
          opacity: 0.5;
          transition: 0.4s;
          cursor: pointer;
        }

        .active {
          opacity: 1;
        }
      }
    }
  }

  .image-container {
    display: flex;
    justify-content: center;
    width: 50%;
    padding-top: 32px;

    .photo {
      display: flex;
      align-items: center;
      width: 230px;
      height: 475px;
      border-radius: 30px;
      box-shadow: 0px 0px 24px 0px #ccc;
      left: 0;
      overflow: hidden;
      position: relative;

      .photo_content {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        position: absolute;
        transition: 0.6s;

        video {
          top: 0;
          right: 50%;
          width: 100%;
          height: 100%;
          position: relative;
        }
      }
    }
  }
  .btn {
    display: none;
    cursor: pointer;
    position: absolute;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background-color: var(--main-color);
    color: var(--background-color);
    transform: translateY(-50%);
    z-index: 2;
  }
  .next {
    inset-inline-start: 0;
    top: 300px;
  }
  .back {
    inset-inline-end: 0;
    top: 300px;
  }
}

.map {
  padding-top: 48px;
  padding-inline: 4%;

  .map_content {
    width: 100%;
    max-width: 960px;
    margin-inline: auto;
    position: relative;

    .map_photo {
      width: 100%;
    }

    .target {
      position: absolute;
      transform: translate(-50%, -50%);
      &:hover {
        .photo {
          display: block;
        }
      }
      .photo {
        display: none;
        width: 200px;
        position: absolute;
        background-color: white;
        border: 1px solid #ccc;
        transform: translate(-50%, -50%);
        top: 330%;
        left: 420%;
        border-radius: 12px;
        transition: 0.4s;
        padding: 12px;
        animation: Fad_In 0.3s ease-in-out;
        p {
          font-size: 12px;
        }

        ::after {
          content: "";
          width: 30%;
          position: absolute;
          height: 2px;
          background-color: var(--main-color);
          transform: translate(-50%, -50%) rotate(40deg);
          top: -30px;
          left: -13%;
        }
        ::before {
          content: "";
          position: absolute;
          height: 16px;
          width: 16px;
          border-radius: 50%;
          background-color: var(--main-color);
          transform: translate(-50%, -50%);
          top: -8px;
          left: 0px;
        }
        img {
          width: 100%;
          border-radius: 12px;
          object-fit: contain;
        }
      }
    }
    .test {
      width: 48px !important;
      height: 48px !important;
      border-radius: 12px;
      background-color: var(--background-color);
      padding: 8px;
    }
  }
}

.video-section {
  padding-top: 48px;
  padding-inline: 4%;
  .video_content {
    width: 100%;
    margin-block: 64px;
    position: relative;
    video {
      width: 100%;
      height: 100%;
      border: 1px solid var(--main-color);
      border-radius: 12px;
    }
    .icon {
      cursor: pointer;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

@media screen and (max-width: 900px) {
  .more_feuters {
    .image-container {
      width: 100%;
      justify-content: center;
    }
  }
  .learn {
    .btn {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .section-container {
      width: 100%;
      .content-wrapper {
        width: 100%;
        .section-title {
          display: none;
        }
        .content-section {
          display: flex;
          flex-direction: column;
          align-items: center;
          max-width: 100%;
        }
        .list {
          justify-content: center;
          text-align: center;

          .list-item {
            display: flex;
            flex-direction: column;
            align-items: center;
            &:not([id="active"]) {
              display: none;
            }
          }
        }
      }
    }
    .image-container {
      width: 100%;
    }
  }
}
@media screen and (max-width: 1024px) {
  .map {
    .map_content {
      .target {
        position: absolute;
        transform: translate(-50%, -50%);
        &:hover {
          .photo {
            visibility: visible;
            opacity: 1;
          }
        }
        .photo {
          position: static;

          width: 200px;
          position: absolute;
          top: 50%;
          left: 50%;

          ::after {
            display: none;
          }
          ::before {
            display: none;
          }
          img {
            width: 100%;
            border-radius: 12px;
            object-fit: contain;
          }
        }
      }
    }
  }
}

@keyframes Fad_In {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
